html {
  font-size: 100%;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -webkit-font-smoothing: antialiased !important;
  text-rendering: optimizelegibility;
  -moz-osx-font-smoothing: grayscale;
}

body {
  font-family: 'Roboto';
}

.container {
  height: 100%;
  width: 100%;
  max-width: 1170px;
}

.row {
  height: 100%;
}

.btn {
  border-radius: 6px;
  background-color: #ef9a3e;
  box-shadow: 0 8px 16px 0 rgba(255, 175, 37, 0.32);
  text-decoration: none;
  color: #ffffff;
  font-size: 16px;
  font-weight: 900;
  letter-spacing: 0.8;
  width: 270px;
  height: 65px;
  text-transform: uppercase;
  font-family: 'Museo Sans';
  position: relative;
  transform: translateX(-50%);
  left: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 0.3s;
}

.btn-primary:hover {
  box-shadow: none;
  background-color: #f0a554;
}

.btn:disabled {
  background-color: #f5f5f5;
  color: #cccccc;
}

.header {
  position: absolute;
  width: 100%;
}

.header__top {
  background: #161c28;
  height: 60px;
  margin-bottom: 15px;
}

.header__lang {
  position: absolute;
  display: flex;
  height: 60px;
  background-color: rgba(255, 255, 255, 0.04);
  display: flex;
  justify-content: center;
  align-items: center;
  right: 0;
  top: 0;
  width: 110px;
  cursor: pointer;
}

.header__lang img {
  width: 22px;
  height: 18px;
}

.header__lang p {
  color: #ffffff;
  margin: 0 8px 0 10px;
}

.header__lang i {
  color: rgba(255, 255, 255, 0.6);
  font-size: 7px;
}

.header nav ul {
  display: flex;
  justify-content: space-between;
}

.header nav ul a {
  color: #ffffff;
  opacity: 0.7;
  text-decoration: none;
  position: relative;
  display: inline-block;
  font-size: 16px;
  transition: all 0.3s;
}

.header nav ul a:before {
  content: '';
  border-top: 3px solid #ef9a3e;
  width: 100%;
  height: 3px;
  display: block;
  position: absolute;
  top: -21px;
  transform: translateX(-100%);
  opacity: 0;
  transition: all 0.3s;
}

.header nav ul a:hover {
  opacity: 1;
}

.header nav ul a:hover:before {
  transform: translateX(0%);
  opacity: 1;
}

.header__social {
  float: right;
  display: flex;
  justify-content: center;
  align-items: center;
}

.header__social a {
  width: 40px;
  height: 40px;
  border-radius: 20.1px;
  background-color: rgba(255, 255, 255, 0.08);
  text-decoration: none;
  font-size: 13px;
  margin-left: 12px;
  transition: all 0.3s;
  display: flex;
  justify-content: center;
  align-items: center;
}

.header__social a i {
  color: rgba(255, 255, 255, 0.7);
  transition: all 0.3s;
}

.header__social a:hover {
  background-color: #ffffff;
}

.header__social a:hover i {
  color: #ef9a3e;
}

.header__logo {
  display: flex;
  text-decoration: none;
  color: #ffffff;
  align-items: center;
}

.header__logo img {
  width: 55px;
}

.header__logo__text {
  margin-left: 10px;
}

.header__logo__text h2 {
  font-size: 27px;
  font-weight: 900;
  font-family: 'Museo Sans';
}

.header__logo__text p {
  color: rgba(255, 255, 255, 0.7);
}

.header__contact {
  float: right;
  text-align: right;
}

.header__contact p {
  font-size: 14px;
  font-weight: 500;
  color: rgba(255, 255, 255, 0.7);
  margin-bottom: 5px;
}

.header__contact a {
  color: #ffffff;
  text-decoration: none;
  display: flex;
  justify-content: center;
  align-items: center;
}

.header__contact a img {
  padding-right: 10px;
}

.header__contact a i {
  color: #ef9a3e;
  background-image: url("../img/sprite.png");
  width: 72px;
  height: 56px;
  background-position: 0px 0px;
}

.intro {
  background: #0E151F url("../img/background/bg-01.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
  height: 720px;
  color: #ffffff;
}

.intro h1 {
  font-size: 62px;
  font-weight: 900;
  font-family: 'Museo Sans';
  line-height: 1.34;
  margin-bottom: 20px;
  margin-top: 80px;
}

.intro p {
  font-size: 20px;
  line-height: 1.5;
  margin-bottom: 50px;
}
